* {
  margin: 0;
  padding: 0;
}

.svg_icons {
  transform: scale(2);
}

.svg_icons:hover {
  transform: scale(3);
  transition: 0.3s;
}

.removeIcon:hover {
  background-color: aliceblue;
  border-radius: 20px;
}

.addIcon:hover {
  background-color: aliceblue;
  border-radius: 20px;
}
